#testimonials {
    margin-bottom: -20px;
}

.testimonials {
    display: flex;
    flex-direction: column;

    width: 85%;
    margin: 100px auto 0 auto;
    padding: 30px 0 60px 0;
    border-top-left-radius: 130px;
    border-top-right-radius: 130px;
    box-shadow: -7px -20px 22px rgba(35, 51, 107, 0.05);

    &__heading {
        text-align: center;
        margin: 0 0 50px 0;

        h2 {
            font-size: 40px;
            font-weight: 900;
            color: var(--primary-bg-color);
        }

        p {
            font-weight: 300;
            margin: 0;
        }
    }

    button {
        margin: 50px auto 0 auto;
    }
}

.testimonial {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 300;
    height: 100px;
    width: 75%;
    margin: 5px auto;

    &__number {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        width: 70px;
        min-width: 70px;
        height: 100%;

        color: rgba(128, 128, 128, 0.5);
        font-weight: 900;
        white-space: nowrap;
       
        position: relative;

        span:first-child {
            margin-bottom: 35px;
        }

        &-shadow {
            font-size: 42px;
            color: #fff;
            text-shadow: 1px 0 0 rgba(128, 128, 128, 0.5), 0 -1px 0 rgba(128, 128, 128, 0.5), 0 1px 0 rgba(128, 128, 128, 0.5), -1px 0 0 rgba(128, 128, 128, 0.5);
            position: absolute;
            left: 0;
            bottom: 15px;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
    }

    &__info {
        display: flex;
        justify-content: space-between;
    }

    &__user {
        height: 70px;
        width: 70px;
        min-width: 70px;
        border-radius: 50%;
        margin: 0 20px auto 20px;
        overflow: hidden;
        position: relative;

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
}