#product-description {

    .product-desc {
        &__tabs {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            background-color: var(--primary-bg-color);
            height: 70px;
    
            .container,
            .row {
                height: 100%;
            }
        }
        &__tab {
            color: white;
            font-weight: 300;
            background: transparent;
            position: relative;
            padding: 0 45px;
    
            span {
                position: relative;
                z-index: 9;
            }

            &-content {
                display: none;
                width: 100%;
                padding: 40px 15px 40px;
    
                &.is-active {
                    display: block;
                }
            }

            &:before {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                transition: opacity .4s ease-in-out;
                background: linear-gradient(139.68deg, rgba(255, 90, 90, 0.17) 21.95%, rgba(255, 135, 67, 0.72) 78%);
                opacity: 0;
            }

            &:hover {
                &:before {
                    opacity: 1;
                }
            }
    
            &.is-active {

                &:before {
                    opacity: 1;
                }
    
                &:after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    height: 2px;
                    width: 100%;
                    background: linear-gradient(95.13deg, #FF5A5A 4.77%, #FF8743 102.57%);
                }
            }
        }
        &__content {
            width: 100%;

            &--heading {
                font-size: 24px;
                font-weight: 900;
                background: -webkit-linear-gradient(90deg, #59CC87 0%, #779E87 105.09%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                margin-bottom: 20px;
            }
    
            p {
                font-size: 14px;
                font-weight: 300;
                font-style: normal;
                line-height: 32px;
            }
    
            strong {
                font-size: 14px;
                font-weight: 900;
            }
        }
    }

    .description {
        &__content {
            p {
                margin-bottom: 10px;
            }
        }
        &__slider {
            margin-top: 40px;
            max-width: 1100px;

            &--1 {
                margin-bottom: 30px;
            }

            .swiper-slide {
                overflow: hidden;
                border-radius: 10px;

                img {
                    height: 100%;
                    width: 100%;
                    object-fit: contain;
                }
            }
        }
    }

    #characteristics {

        .list {
            width: 100%;
            margin-top: 20px;

            td {
                padding: 8px;
            }

            &__item {
                height: 40px;

                &:nth-child(even) {
                    background-color: #f2f2f2;
                }
                &:hover {
                    background-color: #ddd;
                }

                &--title {
                    color: var(--primary-bg-color);
                    font-weight: 900;
                }

                &--content {
                    text-align: right;
                    font-weight: 300;
                }
            }
        }
    }

    .product-testimonials {
        width: 100%;
    
        &__header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 30px;
    
            &-left {
                width: 40%;
                text-align: left;
    
                h3 {
                    font-size: 24px;
                    font-weight: 900;    
                    background: -webkit-linear-gradient(90deg, #59CC87 0%, #779E87 105.09%);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;    
                }
            }
    
            &-right {
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 60%;
    
                h3 {
                    color: grey;
                    font-size: 24px;
                    font-weight: 800;
                    margin-left: auto;
                    margin-right: 30px;            
                }
    
                a {
                    font-size: 16px;
                    font-weight: 700;
                    padding: 10px 20px;
                    border-radius: 20px;
                    color: white;
    
                    &:nth-child(2) {
                        background: linear-gradient(91.55deg, #FCCC50 1.36%, rgba(224, 142, 65, 0.42) 99.79%);
                    }
    
                    &:nth-child(3) {
                        background: linear-gradient(94.19deg, #85E176 3.04%, rgba(91, 202, 135, 0.71) 97.74%);
                    }
    
                    &:nth-child(4) {
                        background: linear-gradient(93.38deg, #D36767 2.86%, rgba(255, 163, 163, 0.49) 99.32%);
                    }
    
                    &:not(:last-child) {
                        margin-right: 15px;
                    }
                }
            }
    
    
        }
    
        &__slider {
            height: 225px;
            margin: 0 -45px;
            padding: 0 45px;
    
            .swiper {
                &-button-prev,
                &-button-next {
                    color: #ddd;
                    transition: color .3s ease;
    
                    &:hover {
                        color: grey;
                    }
                }
    
                &-button-prev, 
                &-rtl &-button-next {
                    left: 0;
                    right: auto;
                }
    
                &-button-next, 
                &-rtl &-button-prev {
                    right: 0;
                    left: auto;
                }
    
                &-pagination {
                    // bottom: -25px;

                    &-bullet-active {
                        background: #59CC87;
                    }
                }
            }
        }

        .testimonial {
            display: flex;
            flex-direction: column;
    
            &__top {
                display: flex;
                align-items: center;
                justify-content: space-between;
                height: 35%;
            }
    
            &__number {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                width: 70px;
                min-width: 70px;
                height: 100%;
    
                color: rgba(128, 128, 128, 0.5);
                font-weight: 900;
                white-space: nowrap;
            
                position: relative;
    
                span:first-child {
                    font-size: 20px;
                    margin-bottom: 35px;
                }
    
                &-shadow {
                    font-size: 42px;
                    color: #fff;
                    text-shadow: 1px 0 0 rgba(128, 128, 128, 0.5), 0 -1px 0 rgba(128, 128, 128, 0.5), 0 1px 0 rgba(128, 128, 128, 0.5), -1px 0 0 rgba(128, 128, 128, 0.5);
                    position: absolute;
                    left: 0;
                    bottom: 0;
                }
            }
    
            &__user {
                height: 75px;
                width: 75px;
                border-radius: 50%;
                overflow: hidden;
    
                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }
    
            &__datetime {
                display: flex;
                flex-direction: column;
                align-items: center;
                font-weight: 300;
    
                span:not(:last-child) {
                    margin-bottom: 10px;
                }
            }
    
            &__bottom {
                height: 65%;
    
                p {
                    font-weight: 300;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 6;
                    overflow: hidden;
                    margin: 0;
                    line-height: 18px;
                }
            }
        }
    
        &__link {
            color: grey;
            text-decoration: underline !important;
        }
    }

    .faq {
        &__heading {
            font-size: 24px;
            font-weight: 900;    
            background: -webkit-linear-gradient(90deg, #59CC87 0%, #779E87 105.09%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
}